.slider {
  position: relative;

  @include onTablet {
    display: none;
  }

  &__arrow {
    position: absolute;
    width: 3vw;
    height: 3vw;
    border: 2vw solid;
    border-color: black transparent transparent black;
    top: 100px;

    &--prev {
      transform: rotate(-45deg);

      left: 20px;
    }

    &--next {
      transform: rotate(135deg);

      right: 20px;
    }
  }
}

.mobile-only {
  @include onTablet {
    display: none;
  }
}
