.menu {

  &__content {
    height: 100vh;
  }

  &__list {
    list-style: none;
    padding: 40px;
  }

  &__cross {
    background-image: url(/images/vector/cross.svg);
    background-repeat: no-repeat;
    height: 24px;
    width: 24px;
  }

  &__item {
    font-family: $secondary-font;
    width: max-content;
    font-size: 24px;
    margin-bottom: 40px;
  }

  &__link {
    color: $main-dark;
  }
}
